<template>
  <x-header :left-options="{showBack: false}"></x-header>
  <x-input title="" name="username" placeholder="" :value.sync="username"></x-input>
  <x-input title="" name="password" type="password" placeholder="" :value.sync="password"></x-input>
  <x-input title="" type="password" placeholder="" :equal-with="password"></x-input>
  <x-input title="" name="email" placeholder="" is-type="email" :value.sync="email"></x-input>
  <cell title="">
    <input id="fileUpload" type="file" accept="image/*" value="" />
  </cell>
  <x-button type="primary" :text="btnText" :disabled="isDisabled" @click="click"></x-button>
  <x-button type="default" text="" @click="login"></x-button>
  <toast :show.sync="showSuccess"></toast>
  <toast :show.sync="showCancel" type="cancel"></toast>
</template>

<script>
import { Cell, XHeader, XInput, XButton, Toast, Group } from 'vux/src/components'
import $ from 'jquery'

export default {
  components: {
    Cell,
    XHeader,
    XInput,
    XButton,
    Toast,
    Group
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      btnText: '',
      username: '',
      password: '',
      email: '',
      isDisabled: false,
      showCancel: false,
      showSuccess: false
    }
  },
  methods: {
    click: function () {
      this.isDisabled = true
      this.btnText = '...'
      let user = new window.Bmob.User()
      user.set('username', this.username)
      user.set('password', this.password)
      user.set('email', this.email)
      user.set('admin', false)
      user.set('unread', 0)
      let fileUploadControl = $('#fileUpload')[0]
      let file = fileUploadControl.files[0]
      let name = file.name
      let fileobj = new window.Bmob.File(name, file)
      user.signUp(null).then((user) => {
        user.set('image', fileobj)
        user.save()
        this.showSuccess = true
        this.isDisabled = false
        this.btnText = ''
        console.log(this.user)
        window.BmobSocketIo.initialize('b5bca53e8b7e474ae626d2d557dc9f78')
        window.BmobSocketIo.updateTable('_User')
        window.BmobSocketIo.onUpdateTable = this.$parent.socketon
        window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': user.id, 'content': '72'}).then((result) => {
          console.log(result)
        }, (error) => {
          console.log('Error!' + error)
        })
        this.$router.go('user')
      }, (user, error) => {
        this.showCancel = true
        this.isDisabled = false
        this.btnText = ''
        console.log('Error!' + error)
      })
    },
    login: function () {
      this.$router.go('user')
    }
  }
}
</script>


